"use client";
import React from "react";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Ghost, Moon, Sun } from "lucide-react";
import { useTheme } from "next-themes";
import { Button } from "@/components/ui/button";

const ProfileCard = ({ name }: { name: string }) => {
  const { theme, setTheme } = useTheme();

  const switchTheme = () => {
    theme === "light" ? setTheme("dark") : setTheme("light");
  };

  return (
    <div className="w-full flex justify-between items-center">
      <div className="flex items-center justify-between gap-4">
        <Avatar>
          <AvatarImage src="https://github.com/shadcn.png" />
          <AvatarFallback>{name.split(" ").map((a) => a[0])}</AvatarFallback>
        </Avatar>
        <h1 className="text-2xl">Welcome, {name}</h1>
      </div>
      <Button
        className="m-0 p-0 w-2"
        size="lg"
        variant="ghost"
        onClick={() => switchTheme()}
      >
        {theme == "dark" && <Sun size={50} strokeWidth={2.5} />}
        {theme != "dark" && <Moon size={50} strokeWidth={2.5} />}
      </Button>
    </div>
  );
};

export default ProfileCard;
